<template>
  <table border="0" cellpadding="0" cellspacing="0" width="750">
    <tbody>
      <tr bgcolor="#FFFFFF" style="color: rgb(34, 34, 34); line-height: 16px; font-family: Arial, Arial, Helvetica, sans-serif; text-transform: uppercase;font-size: 14px;">
        <td align="center">
          <div style="text-align: center;">
            <span v-for="(item,index) in data" :key="index">
              <b v-if="index!==0" style="font-family: '微软雅黑'">&nbsp;-</b>
              <a :href="item.href" target="_blank" style="color: rgb(153, 153, 153); text-decoration-line: none;">{{item.text}}</a>
            </span>
          </div>
        </td>
      </tr>
      <tr height="20">
        <td></td>
      </tr>
    </tbody>
  </table>
</template>
<script>
export default {
  name: 'edmEditor',
  data () {
    return {

    }
  },
  props: {
    data: Array
  }
}
</script>



