<template>
  <div id="edmEditor">
    <div class="edmTemplateBox">
      <component :is="templateList[templateUsing]" :data='data'></component>
    </div>
    <div :class="'closed-button'+(menuOpen?' closed':'')">
      <a class="restoreCode" @click="restore()">重置代码</a>
      <a target="_blank" href="http://mailedit.qedecathlon.lien-sit.decathlon.com.cn/WYSIWYG/indexdec4.php">传图链接</a>
      <div class="close" @click="toggleMenu()">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
    <div :class="'edmRightEditor'+(menuOpen?' closed':'')">
      <draggable :options="{animation:250}" @change="dragEnd()" class="rightFloors" v-model="data">
        <transition-group type="transition">
          <div class="rightFloor" v-for="(floor,indexFloor) in data" :key="floor.component">
            <component :is="floor.component" :data='floor.data'></component>
            <div class="floorNum">楼层{{indexFloor+1}}</div>
            <div class="del" @click="remove(indexFloor)"></div>
            <div class="switch">
              <div class="switch-icon" @click="switchOpen(indexFloor)"></div>
              <div :class="'switch-list'+(switchIndex===indexFloor?' switchActive':'')">
                <div class="switch-list-item" @click="switchFloor(indexFloor, JSON.stringify(item.template))" v-for="(item,index) in switchList" :key="index">{{item.name}}</div>
              </div>
            </div>
          </div>
        </transition-group>
      </draggable>
      <div class="addTr" @click="addTr()">
        新增楼层
        <span></span>
        <span></span>
      </div>
      <div class="addTr generate" @click="generate()">
        复制代码
      </div>
    </div>
    <div :class="'historyBox'+(menuOpen?' closed':'')+(historyOpen?' historyOpen':'')">
      <div class="chooseTemplate">
        <label>选择模板:</label>
        <div :class="'tem'+(index===templateUsing?' active':'')" v-for="(item,index) in templateList" :key="index" @click="useTemplate(index)">{{index+1}}</div>
      </div>
      <div class="historyButton" @click="openHistory()">
        历史记录
      </div>
      <div class="historyList">
        <div class="historyItem" v-for="(item,index) in history" :key="index">
          <span class="delHistory" @click="removeHistory(index)"></span><input type="text" v-model="item.name"><label @click="useHistory(item.data)">使用</label>
        </div>
      </div>
      <div class="saveHistory" @click="saveHistory()">
        保存代码
      </div>
    </div>
  </div>
</template>
<script>
import draggable from 'vuedraggable'
import edmTemplate1 from '../components/edm.template.1.vue'
import edmTemplate2 from '../components/edm.template.2.vue'
import EditorPictureFloor from '../components/editor.switch.picture.vue'
import EditorCategoryFloor from '../components/editor.switch.category.vue'
export default {
  name: 'edmEditor',
  data () {
    return {
      data: [],
      menuOpen: false,
      code: '',
      historyOpen: false,
      history: [],
      templateList: [
        'edmTemplate1',
        'edmTemplate2'
      ],
      switchIndex: -1,
      templateUsing: 0,
      switchList: [
        {
          'name': '默认切图',
          'template': {
            'component': 'EditorPictureFloor',
            'data': [
              {
                'href': '',
                'image': ''
              }
            ]
          }
        },
        {
          'name': '分类链接',
          'template': {
            'component': 'EditorCategoryFloor',
            'data': [
              {
                'href': '',
                'text': ''
              }
            ]
          }
        }
      ]
    }
  },
  components: {
    draggable,
    edmTemplate1,
    edmTemplate2,
    EditorPictureFloor,
    EditorCategoryFloor
  },
  created () {
    if (window.localStorage.getItem('data')) {
      this.data = JSON.parse(window.localStorage.getItem('data'))
    }
    if (window.localStorage.getItem('history')) {
      this.history = JSON.parse(window.localStorage.getItem('history'))
    }
    setInterval(() => {
      this.saveLocalData()
    }, 5000)
  },
  methods: {
    toggleMenu () {
      this.menuOpen = !this.menuOpen
    },
    addTr () {
      this.data.push({
        'component': 'EditorPictureFloor',
        'data': [
          {
            'href': '',
            'image': ''
          }
        ]
      })
    },
    remove (indexFloor) {
      this.data.splice(indexFloor, 1)
    },
    generate () {
      let code = document.querySelector('.edmTemplateBox').innerHTML
      code = code.replace('contenteditable="true"', '')
      const inputBox = document.createElement('input')
      inputBox.style.opacity = 0
      document.body.appendChild(inputBox)
      inputBox.value = code
      inputBox.select()
      document.execCommand('copy')
      document.body.removeChild(inputBox)
      alert('复制成功')
    },
    openHistory () {
      this.historyOpen = !this.historyOpen
    },
    saveHistory () {
      const saveData = this.data
      this.history.push({
        'name': '未命名',
        'data': JSON.stringify(saveData)
      })
      this.saveLocalHistory()
    },
    useHistory (data) {
      this.data = JSON.parse(data)
    },
    removeHistory (index) {
      this.history.splice(index, 1)
      this.saveLocalHistory()
    },
    saveLocalData () {
      const localData = this.data
      window.localStorage.setItem('data', JSON.stringify(localData))
    },
    saveLocalHistory () {
      const localHistory = this.history
      window.localStorage.setItem('history', JSON.stringify(localHistory))
    },
    restore () {
      this.data = []
    },
    useTemplate (index) {
      this.templateUsing = index
    },
    switchOpen (indexFloor) {
      if (this.switchIndex === indexFloor) {
        this.switchIndex = -1
      } else {
        this.switchIndex = indexFloor
      }
    },
    switchFloor (indexFloor, template) {
      this.data.splice(indexFloor, 1, JSON.parse(template))
      this.switchIndex = -1
    }
  }
}
</script>
<style>
  .edmRightEditor{
    position: fixed;
    right: -20px;
    top: 0;
    height: 100%;
    width: 400px;
    background-color: #efefef;
    box-shadow: 0px 0px 8px 3px #c2c2c2;
    z-index: 10;
    transition: all 0.3s;
    padding: 40px 10px 0 10px;
    overflow: auto;
  }

  .closed-button{
    position: fixed;
    top: 0;
    height: 42px;
    right: 0;
    z-index: 15;
    transition: all 0.3s;
    background-color: #efefef;
    width: 380px;
    border-bottom: 1px solid #c2c2c2;
  }

  .historyBox{
    position: fixed;
    bottom: -200px;
    height: 242px;
    right: 0;
    z-index: 15;
    transition: all 0.3s;
    background-color: #efefef;
    width: 380px;
    border-top: 1px solid #c2c2c2;
  }

  .historyBox.historyOpen{
    transform: translateY(-200px);
  }

  .historyBox.closed{
    background-color: transparent;
    opacity: 0;
    border-bottom: none;
    transform: translateX(400px);
  }

  .closed-button a{
    color: #1d1d1d;
    position: absolute;
    left: 100px;
    top: 10px;
    text-decoration: underline;
    font-size: 16px;
    cursor: pointer;
  }

  .closed-button .restoreCode{
    left: 20px;
  }

  .edmRightEditor.closed,.closed-button.closed{
    transform: translateX(400px);
    opacity: 0;
  }

  .closed-button.closed{
    background-color: transparent;
    opacity: 1;
    border-bottom: none;
  }

  .closed-button.closed a {
    display: none;
  }

  .close{
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
    height: 41px;
    cursor: pointer;
    transition: all 0.3s;
  }

  .close span{
    width: 30px;
    height: 3px;
    background-color: #1d1d1d;
    display:block;
    position: absolute;
    border-radius: 3px;
    left: 10px;
    transition: all 0.3s;
  }

  .close span:nth-child(1){
    top: 11px;
    transform: rotate(45deg)translate(0,-3px);
    transform-origin: top left;
  }

  .close span:nth-child(2){
    top: 19px;
    opacity: 0;
  }

  .close span:nth-child(3){
    top: 27px;
    transform: rotate(-45deg)translate(0,3px);
    transform-origin: bottom left;
  }

  .closed .close span{
    transform: none;
    opacity: 1;
  }

  .closed .close{
    transform: translateX(-400px);
  }

  .addTr,.addTd{
    width: 200px;
    height: 40px;
    border: 2px dashed #1d1d1d;
    margin: 10px auto;
    position: relative;
    opacity: 0.8;
    display: flex;
    justify-content: center;
    align-items: center;
    text-indent: 40px;
    font-weight: bold;
    cursor: pointer;
  }
  .addTr{
    transform: translateX(-15px);
  }

  .addTd{
    margin-top: 10px;
    width: 160px;
    height: 30px;
    text-indent: 25px;
    font-size: 14px;
  }

  .addTr span,.addTd span{
    width: 18px;
    height: 4px;
    display: block;
    background-color: #1d1d1d;
    position: absolute;
    left: 60px;
    top: 50%;
    border-radius: 5px;
  }

  .addTr.generate{
    text-indent: 0;
  }

  .addTd span{
    left: 45px;
    width: 14px;
    height: 2px;
  }

  .addTr span:nth-child(1){
    margin-left: -9px;
    margin-top: -2px;
  }

  .addTd span:nth-child(1){
    margin-left: -7px;
    margin-top: -1px;
  }

  .addTr span:nth-child(2){
    transform: rotate(90deg);
    margin-left: -9px;
    margin-top: -2px;
  }

  .addTd span:nth-child(2){
    transform: rotate(90deg);
    margin-left: -7px;
    margin-top: -1px;
  }

  .rightFloor{
    margin: 10px 0 10px 30px;
    width: 290px;
    padding: 10px 10px 0 10px;
    border: 2px solid #1d1d1d;
    border-radius: 5px;
    position: relative;
  }

  .rightFloor .floorNum{
    position: absolute;
    right: -50px;
    width: 50px;
    font-size: 14px;
    top: 0;
  }

  .rightFloor .del{
    width: 22px;
    height: 22px;
    background-color: #d90000;
    border-radius: 50%;
    position: absolute;
    right: -35px;
    top: 50%;
    margin-top: -11px;
    cursor: pointer;
  }

  .rightFloor .del:before{
    content:"";
    width: 14px;
    height: 2px;
    background-color: #fff;
    display: block;
    top: 50%;
    left: 50%;
    position: absolute;
    margin: -1px 0 0 -7px;
  }

  .rightFloorItem{
    border-bottom :1px solid #000;
    margin-top: 5px;
  }

  .rightFloorItem:first-child{
    margin-top: 0;
  }

  .floorItem{
    width: 100%;
    display: flex;
    margin-bottom: 3px;
  }

  .floorItem:nth-child(2){
    margin-bottom: 5px;
  }

  .floorItem label{
    min-width: 44px;
    text-align: left;
    font-weight: bold;
    display: flex;
    align-items: center;
  }

  .floorItem input{
    width: 100%;
    line-height: 24px;
    padding: 0 3px;
    outline: none;
  }
  .historyButton{
    position: absolute;
    top: 0;
    text-decoration: underline;
    left: 20px;
    cursor: pointer;
    width: 200px;
    height: 40px;
    line-height: 40px;
    text-align: left;
  }

  .saveHistory{
    position: absolute;
    right: 20px;
    top: 8px;
    text-decoration: underline;
    cursor: pointer;
  }

  .historyList{
    position: absolute;
    left: 20px;
    right: 20px;
    top: 42px;
    bottom: 10px;
    overflow: auto;
  }

  .historyItem{
    width: 100%;
    display: flex;
    margin-bottom: 5px;
  }

  .historyItem input{
    width: 100%;
    background:transparent;
    border: none;
    outline: none;
    font-size: 16px;
    line-height: 20px;
  }

  .historyItem label{
    cursor: pointer;
    min-width: 50px;
    line-height: 20px;
    font-size: 16px;
  }

  .historyItem span{
    width: 16px;
    height: 16px;
    background-color: #d90000;
    border-radius: 50%;
    cursor: pointer;
    display: inline-block;
    min-width: 16px;
    position: relative;
    top: 3px;
    margin-right: 5px;
  }

  .historyItem span:before{
    content:'';
    width: 10px;
    height: 2px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -1px 0 0 -5px;
    background-color: #fff;
  }
  .chooseTemplate{
    position: absolute;
    background-color: #efefef;
    padding: 0 20px;
    height: 40px;
    top: -41px;
    left: 0;
    width: 100%;
    display: flex;
    line-height: 40px;
    border-top: 1px solid #c2c2c2;
  }
  .tem{
    width: 30px;
    height: 30px;
    background-color: #fff;
    border-radius: 50%;
    margin: 5px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
  }
  .tem.active{
    background-color: #0082c3;
    color: #fff;
  }
  .switch{
    position: absolute;
    left: -37px;
    width: 30px;
    height: 100%;
    top: 0;
  }

  .switch-icon{
    position: absolute;
    top: 50%;
    margin-top: -15px;
    width: 30px;
    height: 30px;
    background: url(/static/切换.svg) no-repeat;
    background-size: 90% auto;
    background-position: center center;
  }

  .switch-list{
    width: 280px;
    height: 100%;
    position: absolute;
    left: 40px;
    background-color: #efefef;
    text-align: left;
    display: none;
  }

  .switch-list.switchActive{
    display: block;
  }

  .switch-list-item{
    display: inline-block;
    padding: 3px 5px;
    margin: 3px;
    border: 1px solid #c2c2c2;
    border-radius: 5px;
    background-color: #fff;
    font-size: 14px;
    cursor: pointer;
  }
</style>



