<template>
  <div>
    <div class="rightFloorItem" v-for="(item,indexItem) in data" :key="indexItem">
      <div class="floorItem">
        <label>链接:</label><input type="text" v-model="item.href" placeholder="请输入链接，若为空表示不加链接">
      </div>
      <div class="floorItem">
        <label>图片:</label><input type="text" v-model="item.image" placeholder="请输入图片链接">
      </div>
    </div>
    <div class="addTd" @click="addTd(data)">
      分割楼层
      <span></span>
      <span></span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'edmEditor',
  data () {
    return {

    }
  },
  props: {
    data: Array
  },
  methods: {
    addTd (data) {
      data.push({
        'href': '',
        'image': ''
      })
    }
  }
}
</script>



