<template>
  <table align="center" bgcolor="#ffffff" border="0" cellpadding="0" cellspacing="0" class="deviceWidth" width="750">
    <tbody>
      <tr>
        <td v-for="(td,indexTd) in data" :key="indexTd">
          <a :href="td.href" v-if="td.href!==''">
            <img :src="td.image" alt="" style="display: block;">
          </a>
          <img :src="td.image" v-else alt="" style="display: block;">
        </td>
      </tr>
    </tbody>
  </table>
</template>
<script>
export default {
  name: 'edmEditor',
  data () {
    return {

    }
  },
  props: {
    data: Array
  }
}
</script>



